import axios from 'axios';
import QS from 'qs';
const $axios = axios.create({
  baseURL: process.env.REACT_APP_BASE_URL,
  timeout: 4000
});
const baseURL = 'http://tiantianxsg.com:39888/admin.php'
const baseURL1 = 'http://tiantianxsg.com:39888/index.php'
//
$axios.interceptors.request.use(
  function (config) {
    // 
    // reudxstoretoken status
    // token
    const token = localStorage.getItem('token');
    if (localStorage.getItem('token')) {
      config.headers['HTTP_TOKEN'] = token
      // config.headers['HTTP-USERID'] = 6
    }
    config.headers = { 'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8' }
    return config;
  },
  function (error) {
    // 
    // this.$message.error(error);
    return Promise.reject(error);
  }
);
// 
$axios.interceptors.response.use(
  function (response) {
    // 
    if (response.data.status.code === 200) {
      return Promise.resolve(response);
    } else {
      return Promise.reject(response);
    }
    // return Promise.resolve(response); 
  },
  // 200 
  error => {
    console.log(error.response)
    if (error.response && error.response.status) {
      switch (error.response.status) {
        // 401:     
        //     
        //     
        case 401:
          break;
        // 403 token    
        //     
        // tokenvuextoken    
        //     
        case 403:
          this.$message.error(error.response.status.remind);
          // token     
          localStorage.removeItem('token');
          // fullPath     
          break;
        // 404    
        case 404:
          //   this.$message.error('');   
          break;
        //     
        default:
        //   this.$message.error(error.response.status.remind) 
      }
      return Promise.reject(error.response);
    }
  }
);
/** 
 * postpost 
 * @param {String} url [url] 
 * @param {Object} params [] 
 */
export function post (url, data) {
  return new Promise((resolve, reject) => {
    $axios.post(`${baseURL}${url}`, QS.stringify(data || {}))
      .then(res => {
        resolve(res.data)
      })
      .catch(err => {
        console.log(err)
        reject(err.data)
      })
  });
}
export function $post (url, params) {
  return new Promise((resolve, reject) => {
    $axios.post(`${baseURL}${url}${'?' + QS.stringify(params)}`)
      .then(res => {
        resolve(res.data)
      })
      .catch(err => {
        reject(err.data)
      })
  });
}
export function upload (params) {
  let file = new FormData()
  file.append('image', params)
  return new Promise((resolve, reject) => {
    $axios.post(`${baseURL1}/uploadimg/moreupload`, file)
      .then(res => {
        resolve(res.data)
      })
      .catch(err => {
        reject(err.data)
      })
  });
}
export function getData (url, params) {
  return new Promise((resolve, reject) => {
    $axios.post(`${baseURL1}${url}${'?' + QS.stringify(params)}`)
      .then(res => {
        resolve(res.data)
      })
      .catch(err => {
        reject(err.data)
      })
  });
}
