<template>
  <div class="member-table">
    <div class="action-btn x-flex-between">
      <div>
        <el-button type="primary" icon="el-icon-plus" @click="addMember"></el-button>
        <el-button></el-button>
        <el-button></el-button>
        <span class="select-text">
          
          <el-button type="text">{{multipleSelection.length}}&nbsp;</el-button>
        </span>
        <el-button type="text" @click="multipleSelection=[]"></el-button>
      </div>
      <div>
        <el-button @click="dismissTeam()"></el-button>
      </div>
    </div>
    <div class="table">
      <el-table
        border
        :data="tableData"
        ref="multipleTable"
        style="width: 100%"
        @selection-change="handleSelectionChange"
      >
        <el-table-column label="" type="selection" align="center" width="60"></el-table-column>
        <el-table-column label="" align="center" width="150">
          <template slot-scope="props">
            <el-button type="text" @click="handleEdit(props.row)">{{props.row.user_name}}</el-button>
          </template>
        </el-table-column>
        <el-table-column label="" prop="mobile" align="center" width="150"></el-table-column>
        <el-table-column label="" prop="depart_name" align="center" width="150"></el-table-column>
        <el-table-column label="" prop="grade_name" sortable align="center" width="150"></el-table-column>
        <el-table-column label="" prop="grade_name" align="center" width="150"></el-table-column>
        <el-table-column label="" prop="entry_num" align="center" width="150"></el-table-column>
        <el-table-column label="" prop="entry_num" align="center" width="150"></el-table-column>
        <el-table-column label="" align="center" width="260">
          <template slot-scope="props">
            <span>{{ props.row.logout_time ? $moment(props.row.logout_time).format('YYYY-MM-DD HH:mm'): '--'}}</span>
          </template>
        </el-table-column>
        <el-table-column label="" align="center" width="150">
          <template slot-scope="props">
            <span
              class="status"
              :class="{'active-status':props.row.status==1}"
            >{{props.row.status==1?"":''}}</span>
          </template>
        </el-table-column>
        <el-table-column label="" align="center" width="150">
          <template slot-scope="scope">
            <el-button @click="handleEdit(scope.row)" type="text" size="small"></el-button>
            <el-button @click="handleDel(scope.row)" type="text" size="small"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    tableData: {
      type: []
    }
  },
  props: ['tableData'],
  data () {
    return {
      memberInfo: {},
      teamId: '',
      multipleSelection: []
    }
  },
  methods: {
    addMember () {
      this.$emit('addMember')
    },
    handleEdit (row) {
      // this.memberInfo = row
      // this.teamId = row.uid
      this.$emit('handleEdit', row.uid)
    },
    handleSelectionChange (val) {
      this.multipleSelection = val;
      this.$emit('handleSelectionChange', this.multipleSelection.length)
    },
    handleDel (row) {
      // this.memberInfo = row
      // this.teamId = row.uid
      this.$emit('handleDel', row.uid)
    },
    dismissTeam () {
      this.$emit('dismissTeam')
    }
  }
}
</script>
<style lang="scss">
  .table {
    border-top: 1px solid #eee;
    border-bottom: 1px solid #eee;
    padding: 10px 0;
    height: 76%;
  }
  .status {
    position: relative;
    margin-left: 10px;
    &.active-status{
      &::before{
       background: #71D875
     }
    }
    &::before{
      position: absolute;
      content: "";
      width:6px;
      height: 6px;
      border-radius: 50%;
      top: 7px;
      left: -20px;
      background: #FF0000;
    }
  }
</style>