<template>
  <div>
    <p class="company-title"></p>
    <section class="section-box">
      <el-form-item label="" prop="team_name">
        <el-input v-model="userInfo.name" class="width408" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-button type="primary"></el-button>
        <span class="password-tip">
          <i class="el-icon-warning-outline" /> :12345
        </span>
      </el-form-item>
    </section>
  </div>
</template>
<script>
import { resetPassword } from '../api/company'
export default {
  data () {
    return {
      userInfo: {}
    }
  },
  created () {
    if (localStorage.getItem('userInfo')) {
      this.userInfo = JSON.parse(localStorage.getItem('userInfo'))
    }
    console.log(this.userInfo)
  },
  methods: {
    handlePassword () {
      this.$alert('123456<br>?', '', {
        dangerouslyUseHTMLString: true,
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        let uid = this.userInfo.id
        resetPassword({ uid }).then(res => {
          this.$message.success('')
          this.getList(this.formParams)
        })
      }).catch(() => {
        console.log(2)
      })
    },
  }
}
</script>